<script>
import PersonInfo from '../components/PersonInfo.vue'
import CarInfo from '../components/CarInfo.vue'
import BackSize from '../components/BackSize.vue'
import PadSize from '../components/PadSize.vue'
import BackHard from '../components/BackHard.vue'
import PadHard from '../components/PadHard.vue'

import { mapState } from "vuex";

import api from '../api/index'
export default {
  name: 'home',
  components: {
    PersonInfo,
    CarInfo,
    BackSize,
    PadSize,
    BackHard,
    PadHard
  },
  data() {
    return {
      fileList: [],
      
      carBrandList: [],
      carLevel: [],
      carPrice: [],
      chairColor: [],
      chairMaterial: [],
      backrestSize: [],
      cushionSize: [],
      hardnessSize: []
    }
  },
  computed: {
      ...mapState({
          user_info: state => state.user_info,
          sit_base_info: state => state.sit_base_info,
      })
  },
  created() {
    this.tableInfo();
  },
  methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      tableInfo() {
        api.tableInfo().then(res => {
          console.log(res.data.data)
          let data = res.data.data;
          this.carBrandList = data.carBrandList;
          this.carLevel = data.carLevel;
          this.carPrice = data.carPrice;
          this.chairColor = data.chairColor;
          this.chairMaterial = data.chairMaterial;

          // 
          let backrestSizeArr = [];
          data.backrestSize.forEach(item => {
            backrestSizeArr.push({
                name: item.title,
                id: item.field,
                tips: item.explain,
                tipsImg: '',
                tipsShow: false,
                err: '',
                errMsg: '',
                val: ''
            })
          })
          this.backrestSize = backrestSizeArr;

          // 
          let cushionSizeArr = [];
          data.cushionSize.forEach(item => {
            cushionSizeArr.push({
              name: item.title,
              id: item.field,
              tips: item.explain,
              tipsImg: item.image,
              tipsShow: false,
              err: '',
              errMsg: '',
              val: ''
            })
          })
          this.cushionSize = cushionSizeArr;

          // 
          let hardnessSizeArr = [];
          data.hardnessSize.forEach(item => {
            hardnessSizeArr.push({
              name: item.title,
              id: item.field,
              tips: item.explain,
              tipsImg: item.image,
              tipsShow: false,
              err: '',
              errMsg: '',
              val: ''
            })
          })
          this.hardnessSize = hardnessSizeArr;
            
        })
      }
  }
}
</script>
